.user-info {
  width: 100%;
  min-height: 555px;
  padding: 0 40px;
  background-image: url("../img/jpeg/background-form.jpg");
  background-color: $color-light-blue;
  background-size: cover;
  background-position: 0 4%;

  @include retina {
    background-image: url("../img/jpeg/background-form@2x.jpg");
  }

  @include vp-1023 {
    min-height: 805px;
  }

  @include vp-767 {
    min-height: 760px;
    padding: 0 15px;
  }
}

.user-info__wrapper {
  display: flex;
  justify-content: space-between;
  max-width: 1056px;
  margin: 0 auto;
  padding-top: 50px;

  @include vp-1023 {
    padding-top: 45px;
    flex-direction: column;
  }
}

.user-info__wrapper-text {
  padding: 15px;
  padding-top: 163px;
  width: 50%;

  @include vp-1023 {
    width: 100%;
    padding: 15px;
    padding-top: 44px;
    padding-bottom: 52px;
  }

  @include vp-767 {
    padding: 15px;
    margin-bottom: 17px;
  }
}

.user-info__wrapper h2 {
  margin: 0;
  margin-bottom: 8px;
  font-family: $Montserrat;
  font-weight: 700;
  font-size: 40px;
  line-height: 150%;
  text-align: center;

  @include vp-1023 {
    font-size: 34px;
    margin-bottom: 4px;
  }

  @include vp-767 {
    font-size: 24px;
    line-height: 130%;
    margin-bottom: 13px;
  }
}

.user-info__wrapper p {
  width: 70%;
  margin: 0 auto;
  font-family: $Montserrat;
  font-weight: 400;
  font-size: 18px;
  line-height: 150%;

  br {
    display: none;
  }

  @include vp-1023 {
    width: 59%;
    text-align: center;
  }

  @include vp-767 {
    width: 100%;
    font-size: 16px;
    line-height: 140%;

    br {
      display: block;
    }
  }
}

.user-info__form {
  min-height: 455px;
  padding: 49px;
  padding-top: 45px;
  width: 38%;
  background-color: $color-dark-blue;

  @include vp-1023 {
    width: 59%;
    margin: 0 auto;
  }

  @include vp-767 {
    width: 100%;
    margin: 0;
    padding: 35px 30px;
    min-height: 460px;
  }
}

.user-info__form h3 {
  margin: 0;
  margin-bottom: 26px;
  font-family: $Montserrat;
  font-weight: 500;
  font-size: 18px;
  line-height: 150%;
  color: $color-white-grey;

  br {
    display: none;
  }

  @include vp-767 {
    width: 95%;
    margin: 0 auto;
    line-height: 140%;
    text-align: center;
    margin-bottom: 28px;

    br {
      display: block;
    }
  }
}
